$red          : #eb0000;
$green        : #649632;
$darkerGreen  : #2d8434;
$tan          : #aa782c;

@font-face {
  font-family: 'Great Vibes';
  src: url('../public/fonts/GreatVibes-Regular.ttf') format('truetype'); 
  font-weight: normal;
  font-style: normal;
}

// keyframes mixin
@mixin keyframes($name) {
  @-webkit-keyframes #{$name} {
    @content; 
  }
  @-moz-keyframes #{$name} {
    @content;
  }
  @-ms-keyframes #{$name} {
    @content;
  }
  @keyframes #{$name} {
    @content;
  } 
}

@mixin animate($name, $time:1s, $algor:linear, $delay:"", $loop:infinite,$fill:"") {
  -webkit-animation: #{$name} $time $algor #{$delay} $loop #{$fill};
	-moz-animation: #{$name} $time $algor #{$delay} $loop #{$fill};
	-ms-animation: #{$name} $time $algor #{$delay} $loop #{$fill};
	animation: #{$name} $time $algor #{$delay} $loop #{$fill};
}

@mixin transform($transform) {
  -webkit-transform: $transform;
  -moz-transform: $transform;
  -ms-transform: $transform;
  transform: $transform;
}

@mixin icon($size) {
    position: absolute;
    display: block;
    font-size: $size;
    line-height: $size;
    height: $size;
    width: $size;
}

@include keyframes(star-flash) {
